import React from 'react';
import { connect } from 'react-redux';
import { Card, Table, Tag, Tooltip, message, Button, Col, Row, Popconfirm, Radio } from 'antd';
import { DownloadOutlined, EyeOutlined, RollbackOutlined, UploadOutlined } from '@ant-design/icons';
import axios from 'axios';

const ForwardedDeals = (props) => {
    let { user_id, role_id, history } = props;

    let [loadingForwarded, setLoadingForwarded] = React.useState(true);
    let [loadingPicked, setLoadingPicked] = React.useState(true);
    let [forwardedDeals, setForwardedDeals] = React.useState([]);
    let [pickedDeals, setPickedDeals] = React.useState([]);

    const columns = [
        {
            title: 'Reference No.',
            dataIndex: 'order_token',
            key: 'order_token',
            align: 'center',
            render: text => <span>
                <Tag >
                    {text}
                </Tag>
            </span>,
        },
        {
            title: 'Client Name',
            dataIndex: 'client_contactperson',
            key: 'client_contactperson',
            align: 'center',
        },
        {
            title: 'Client Email',
            dataIndex: 'client_email',
            key: 'client_email',
            align: 'center',
        },
        {
            title: 'Created By',
            dataIndex: 'user_name',
            key: 'user_name',
            align: 'center',
        },
        {
            title: 'Status',
            dataIndex: 'orderstatus_name',
            key: 'orderstatus_name',
            align: 'center',
            render: (tag, record) => {
                let color = 'red';
                if (record.orderstatus_id == '1') color = 'blue'
                else if (record.orderstatus_id == '2') color = 'magenta'
                else if (record.orderstatus_id == '3') color = 'geekblue'
                else if (record.orderstatus_id == '4') color = 'purple'
                else if (record.orderstatus_id == '5') color = 'lime'
                else if (record.orderstatus_id == '6') color = '#f77474'
                else if (record.orderstatus_id == '7') color = 'blue'
                else if (record.orderstatus_id == '8') color = 'orange'
                else if (record.orderstatus_id == '9') color = 'gold'
                else if (record.orderstatus_id == '10') color = 'cyan'
                else if (record.orderstatus_id == '11') color = 'green'
                else if (record.orderstatus_id == '12') color = 'volcano'
                else if (record.orderstatus_id == '13') color = 'red'
                return (
                    <span>
                        <Tag color={color}>
                            {tag}
                        </Tag>
                    </span>
                )
            },
        },
        {
            title: 'Action',
            key: 'action',
            align: 'center',
            render: (text, record) => (
                record.orderstatus_id == 2 ?
                    <div>
                        <Tooltip title="Pick Deal">
                            <Button
                                type="primary"
                                className="mr-2"
                                icon={<DownloadOutlined />}
                                size="small"
                                onClick={() => pickDeal(record.order_token)}
                            />
                        </Tooltip>
                    </div>
                    :
                    <div>
                        <Tooltip title="View Deal">
                            <Button
                                type="primary"
                                className="mr-2"
                                icon={<EyeOutlined />}
                                size="small"
                                onClick={() => history.push(`/orders/${record.order_token}`)}
                            />
                        </Tooltip>
                        {
                            record.orderstatus_id == 3 &&
                            <Tooltip title="Unpick Deal">
                                <Button
                                    type="primary"
                                    danger
                                    className="mr-2"
                                    icon={<UploadOutlined />}
                                    size="small"
                                    onClick={() => unPickDeal(record.order_token)}
                                />
                            </Tooltip>
                        }
                        {
                            record.orderstatus_id == 5 &&
                            <Tooltip title="Return To Agent">
                                <Button
                                    type="primary"
                                    className="mr-2"
                                    icon={<RollbackOutlined />}
                                    size="small"
                                    style={{ background: 'green', borderColor: 'green' }}
                                    onClick={() => backToAgent(record.order_token)}
                                />
                            </Tooltip>
                        }
                    </div>
            )
        },
    ];

    const getForwardedDeals = async () => {
        try {
            let res = await axios.post('/forwardeddeallist', { user_id });
            setForwardedDeals(res.data.data);
            setLoadingForwarded(false);
        } catch (error) {
            console.log(error);
            setLoadingForwarded(false);
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message)
        }
    }

    const getPickedDeals = async () => {
        try {
            let res = await axios.post('/pickdeallist', { user_id });
            setPickedDeals(res.data.data);
            setLoadingPicked(false);
        } catch (error) {
            console.log(error);
            setLoadingPicked(false);
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message)
        }
    }

    const pickDeal = async (order_token) => {
        try {
            let res = await axios.post('/updateorderstatusmanager', { user_id, order_token, orderstatus_id: 3 });
            getForwardedDeals();
            getPickedDeals();
            message.success(res.data.message);
        } catch (error) {
            console.log(error);
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message)
        }
    }

    const unPickDeal = async (order_token) => {
        try {
            let body = {
                user_id,
                role_id,
                order_token
            }

            let res = await axios.post('/unpickorder', body);
            getForwardedDeals();
            getPickedDeals();
            message.success(res.data.message);
        } catch (error) {
            console.log(error);
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message)
        }
    }

    const backToAgent = async (order_token) => {
        try {
            let res = await axios.post('/updateorderstatusmanager', { user_id, order_token, orderstatus_id: 6 });
            getPickedDeals();
            message.success(res.data.message);
        } catch (error) {
            console.log(error);
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message)
        }
    }

    React.useEffect(() => {
        getForwardedDeals();
        getPickedDeals();
    }, []);

    return (
        <>
            <Card>
                <h3>Forwarded Orders</h3>
            </Card>
            <Row style={{ justifyContent: 'center' }}>
                <Col lg={22}>
                    <Card bodyStyle={{ 'padding': '0px' }}>
                        <Table
                            columns={columns}
                            dataSource={forwardedDeals}
                            loading={loadingForwarded}
                            rowKey='order_token'
                        />
                    </Card>
                </Col>
            </Row>
            <Card>
                <h3>Picked Orders</h3>
            </Card>
            <Row style={{ justifyContent: 'center' }}>
                <Col lg={22}>
                    <Card bodyStyle={{ 'padding': '0px' }}>
                        <Table
                            columns={columns}
                            dataSource={pickedDeals}
                            loading={loadingPicked}
                            rowKey='order_token'
                        />
                    </Card>
                </Col>
            </Row>
        </>
    )
}

const mapStateToProps = state => ({
    user_id: state.auth.user.user_id,
    role_id: state.auth.user.role_id,
});

export default connect(mapStateToProps)(ForwardedDeals);